.slide-right-enter-active,
.slide-right-leave-active,
.slide-left-enter-active,
.slide-left-leave-active {
	will-change: transform;
	-webkit-transition: all .3s;
	transition: all .3s;
	position: absolute;
	width:100%;
	left:0;
}
.slide-right-enter {
	-webkit-transform: translateX(-100%);
	        transform: translateX(-100%);
}
.slide-right-leave-active {
	-webkit-transform: translateX(100%);
	        transform: translateX(100%);
}
.slide-left-enter {
	-webkit-transform: translateX(100%);
	        transform: translateX(100%);
}
.slide-left-leave-active {
	-webkit-transform: translateX(-100%);
	        transform: translateX(-100%);
}

